import { Component, OnInit ,Injector, QueryList, ElementRef, ViewChildren} from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { MatTableDataSource } from '@angular/material/table';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
import {Observable } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import {FormHelperService} from '../../../../../service/form-helper.service';
import { Materialstore } from 'src/app/interface/receipt/materialstore';
import { BaseForm } from 'src/app/class/base.form';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './material-store.component.html',
  styleUrls: ['./material-store.component.css']
})
export class MaterialStoreComponent extends BaseForm implements OnInit {
  @ViewChildren("checkboxes") checkboxes: QueryList<ElementRef>;
  @ViewChildren("parencheckbox") parencheckbox: QueryList<ElementRef>;
  public componentName : string;
  public displayedColumns = ['S No', 'Select', 'Receipt no', 'item' , 'quantity','description' ,'amount'];
  public dataSource = new MatTableDataSource();
  public showTable = false;
  public multiSelect = false;
  public selectAllList : Array<any | number> =[];
  public su;
  public filter_data = {} ;
  public idList = [] ;
  public totalRecords : number;

  constructor(
    public injector : Injector,
    private hepler : FormHelperService
    ){
    super(injector);
  }

  ngOnInit(): void {
    this.componentName = "Material Store Placement";
    this.initForm();
  }

  initForm() {
    this._fs._form = this._fb.group({
      store_location: ['',this._vs._vals('Location (store)')],
      placement_ids: this._fb.array([]),
    });
  }

  getStoreFilter(event : any){
    this.filter_data = event;
    if(event !== '' && event !== undefined && event !== null){
      if((this._fs._form.get('placement_ids') as FormArray) != null){
        (this._fs._form.get('placement_ids') as FormArray).clear();
      }
      this.su = event.su;
      this._http.get({
        url: environment.TRANSACTION,
        endpoint: URLz.MATERIAL_STORE_LIST,
        query: {
          bg:event.bg,
          le:event.le,
          ou:event.ou,
          su:event.su,
          donation_category:event.donation_category,
        }
      }).subscribe((res : any)=>{
        this.dataSource.data = res.data.records;
        this.selectAllList = res.data.records;
        this.totalRecords = res?.data.totalRecords;
        res?.data?.records.length > 0 ? (this.showTable = true) : (
          this.showTable = false,
          this._swl.noDataFound()
        );
      })
    }
  }

  uncheckAll() {
    this.checkboxes.forEach((element) => {
      element.nativeElement.checked = false;
    });
  }

  selectAll(){
    const selectedPerArr: FormArray = this._fs._form.get('placement_ids') as FormArray;
    this.multiSelect = !this.multiSelect;
    if(this.multiSelect){
      if(this.selectAllList.length != 0){
        this.selectAllList.forEach(val => {
          selectedPerArr.push(new FormControl(val.id));
        });
      }
    }else{
      while (0 !== selectedPerArr.length) {
        selectedPerArr.removeAt(0);
      }
    }
  }

  getcheckBoxValue(event,eId){
    const selectedPerArr: FormArray = this._fs._form.get('placement_ids') as FormArray;
    if(event.target.checked){
      selectedPerArr.push(new FormControl(eId));
    }
    else{
      let i = 0;
      selectedPerArr.controls.forEach((ctrl: FormControl) => {
        if(ctrl.value == eId) {
          selectedPerArr.removeAt(i);
          return;
        }
        i++;
      });
    }
     this.indeterminate(selectedPerArr.value);
  }

  indeterminate(checked_arr){
    if(checked_arr.length == this.totalRecords){
      this.parencheckbox.forEach((element) => {
        console.log(element);
        element.nativeElement.checked = true;
      });
    }
    else if(checked_arr.length == 0){
      this.parencheckbox.forEach((element) => {
        console.log(element);
        element.nativeElement.checked = false;
      });
    }
  }

  checksForSubmit(){
    this.multiSelect = false;
    this.uncheckAll();
    if((this._fs._form.get('placement_ids') as FormArray) != null){
      (this._fs._form.get('placement_ids') as FormArray).clear();
    }
  }

  _onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if(this._fs._form.value.placement_ids.length == 0){
      this.hepler.openSnackBar(this._translate.instant('Please select one checkbox') ,'close','right','top');
    }else{
      if (this._fs._form.valid){
        const data: Materialstore = this._fs._form.getRawValue();
        Swal.fire({
          title: this._translate.instant('Are you sure?'),
          text: this._translate.instant("You won't be able to revert this!"),
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: this._translate.instant("Yes, Proceed!"),
          cancelButtonText: this._translate.instant("Cancel")
        }).then((result) => {
          if (result.isConfirmed) {
              let modify: Observable<any>;
              modify = this._http.create({body: data , url: environment.TRANSACTION ,endpoint: URLz.MATERIAL_STORE_MAP});
              modify.subscribe({
                next: (res: any) => {
                    if(res.data != undefined && res.data != null){
                      if(res?.data?.errors?.length > 0){
                        for(let i=0 ; i<res?.data?.errors?.length ; i++){
                          this.idList[i] = res?.data.errors[i]?.fund_category[0]?.id;
                        }

                          this.checksForSubmit();
                          if(res?.data?.success?.length > 0){
                            Swal.fire({
                              title: this._translate.instant('Processed'),
                              text: this._translate.instant(res.message),
                              icon: 'success',
                              confirmButtonColor: '#3085d6',
                              confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
                            })
                          }
                          this.getStoreFilter(this.filter_data);
                          this._vs._error_from_success(data);
                      }
                      else{
                        this.checksForSubmit();
                        if(res?.data?.success?.length > 0){
                          Swal.fire({
                            title: 'Processed',
                            text: res.message,
                            icon: 'success',
                            confirmButtonColor: '#3085d6',
                            confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
                          })
                        }
                        this.getStoreFilter(this.filter_data);
                      }
                    }
                  },
                  error: (httpErrorResponse: HttpErrorResponse) => {
                    this._vs._error_server(httpErrorResponse.error);
                  }
              });
              this._vs._submitted = false;
          }
        })
      }else{
        return this._fs._form.valid;
      }
    }
  }

}
