import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CashDepositBankComponent } from './cash-deposit-bank/cash-deposit-bank.component';
import { ChequeDepositBankComponent } from './cheque-deposit-bank/cheque-deposit-bank.component';
import { MaterialStoreComponent } from './storePlacement/material-store/material-store.component';
import { ChequeClearanceComponent } from './cheque-clearance/cheque-clearance.component';
import { CashDepositOfficeComponent } from './cash-deposit-office/cash-deposit-office.component';
import { ACTION } from 'src/app/enums/action.enum';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'cash_deopsit_bank',
        pathMatch: 'full'
      },
      {
        path: "cash_deopsit_bank",
        component: CashDepositBankComponent,

        data: {
          title: "Cash Deopsit In Bank",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Cash Deopsit In Bank"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "cash_deopsit_office",
        component: CashDepositOfficeComponent,

        data: {
          title: "Cash Deopsit In Office",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Cash Deopsit In Office"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "cheque_deopsit_bank",
        component: ChequeDepositBankComponent,
        data: {
          title: "Cheque Deopsit In Bank",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Cheque Deopsit In Bank"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "cheque_clearance",
        component: ChequeClearanceComponent,

        data: {
          title: "Cheque Clearance",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Cheque Clearance"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "material_store_placement",
        component: MaterialStoreComponent,

        data: {
          title: "Material Store Placement",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Material Store Placement"},
            { title: "Add" },
          ]
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DepositRoutingModule { }
